<template>
    <div class="entries">
        <h3>Гостевая книга</h3>
        <div class="entries-info">
            <div class="stats">Всего: {{ total }} записей</div>
            <div class="block-button">
                <button type="button" class="entry-button" @click="openAddForm">
                    <template v-if="formVisible">
                        Закрыть форму
                    </template>
                    <template v-else>
                        Добавить запись
                    </template>
                </button>
            </div>
        </div>
        <form class="entry-form" v-if="formVisible" @submit.prevent="send">
            <div class="input-group">
                <label>Имя</label>
                <input type="text" v-model="form.name" name="name" required>
            </div>
            <div class="input-group">
                <label>Email</label>
                <input type="email" v-model="form.email" name="email" required>
            </div>
            <div class="input-group">
                <label>Сообщение</label>
                <textarea name="content" v-model="form.content" maxlength="255" required></textarea>
            </div>
            <img src="/captcha.php" v-if="reloadCaptcha">
            <div class="input-group">
                <label>Код</label>
                <input type="text" v-model="form.captcha" name="captcha" required>
            </div>
            <button type="submit" class="entry-button">
                Отправить
            </button>
        </form>
        <transition-group name="entry" tag="div">
            <div class="entry" v-for="(entry, index) in entries" :key="entry.id">
                <form class="entry-form" v-if="formDelete.index == index" @submit.prevent="remove">
                    <div class="input-group">
                        <label>Пароль</label>
                        <input type="password" v-model="formDelete.password" name="password" required>
                    </div>
                    <img src="/captcha.php" v-if="reloadCaptcha">
                    <div class="input-group">
                        <label>Код</label>
                        <input type="text" v-model="formDelete.captcha" name="captcha" required>
                    </div>
                    <button type="submit" class="entry-button">
                        Удалить
                    </button>
                </form>
                <div class="entry-header">
                    {{ entry.name }}
                    <button type="button" class="entry-button delete-button" @click="showDeleteForm(index)">
                        x
                    </button>
                </div>
                <div class="entry-content">{{ entry.content }}</div>
                <div class="entry-footer">
                    <div class="email">{{ entry.email }}</div>
                    <div class="date">{{ entry.created_at }}</div>
                </div>
            </div>
        </transition-group>
        <button type="button" class="entry-button" @click="loadMore" v-if="total > entries.length">
            Загрузить еще
        </button>
    </div>
</template>

<script>
    import { fetchEntries, addEntry, deleteEntry } from '../api.js'

    export default {

      data() {
        return {
          entries: [],
          total: 0,
          query: {
            offset: 0,
            limit: 20
          },
          password: '',
          formVisible: false,
          reloadCaptcha: true,
          form: {
            name: '',
            email: '',
            content: '',
            captcha: ''
          },
          formDelete: {
            password: '',
            id: null,
            captcha: '',
            index: null
          }
        }
      },

      methods: {

        loadMore() {
          this.query.offset = this.entries.length;
          fetchEntries(this.query)
            .then((response) => {
                this.total = response.total;
                this.entries.push(...response.data)
            })
        },

        openAddForm() {
          this.formVisible = !this.formVisible;
          if(this.formVisible) {
            this.formDelete.id = null;
            this.formDelete.index = null;
          }
        },

        send() {
          addEntry(this.form)
            .then((response) => {
                if(!response.success) {
                    alert(response.text);
                    this.form.captcha = '';
                    this.reloadCaptcha = false;
                    this.$nextTick(() => {
                      this.reloadCaptcha = true;
                    })
                    return;
                }
                this.total++;
                this.entries.unshift(response.data);
                this.formVisible = false;
                this.resetForm()
            })
        },

        remove() {
          deleteEntry(this.formDelete)
             .then((response) => {
                 if(!response.success) {
                    alert(response.text);
                    this.formDelete.captcha = '';
                    this.reloadCaptcha = false;
                    this.$nextTick(() => {
                      this.reloadCaptcha = true;
                    })
                    return;
                 }
                 var index = this.formDelete.index;
                 this.formDelete.captcha = '';
                 this.formDelete.id = null;
                 this.formDelete.index = null;
                 this.entries.splice(index, 1)
                 this.total--;
             })
        },

        showDeleteForm(index) {
          var id = this.entries[index].id;
          if(this.formDelete.id == id) {
            this.formDelete.id = null;
            this.formDelete.index = null;
          } else {
            this.formDelete.id = id;
            this.formDelete.index = index;
            this.formVisible = false;
          }
        },

        resetForm() {
          this.form = {
            name: '',
            email: '',
            content: '',
            captcha: ''
          }
        }
      },

      created() {
        this.loadMore()
      }

    }
</script>

